<template>
    <div class='comingSoon'>
        <ul>
            <li v-for='data in filmslist' :key='data.filmId' @click='handleClick(data.filmId)'>
                <a class='fbox'>
                    <img :src='data.poster' class='left' />
                    <div class='center'>
                        <div class='info'>
                            <span class='name'>{{data.name}}</span>
                            <span class='item'>{{data.filmType.name}}</span>
                        </div>
                        <div class='info'>
                            <span class='label'>主演:</span>
                            <span v-for='actor in data.actors' :key='actor.name' class='label'>{{actor.name}}</span>
                        </div>
                        <div class='info'>
                            <span class='label'>{{data.nation}} | {{data.runtime}}分钟</span>
                        </div>
                    </div>
                </a>
            </li>
        </ul>
        <div class='nomore'>
            无更多电影
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      filmslist: []
    }
  },
  mounted () {
    axios({
      url: 'https://m.maizuo.com/gateway?cityId=310100&pageNum=1&pageSize=10&type=2&k=1907560',
      method: 'get',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1582288622450971566184","bc":"310100"}',
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then(res => {
      this.filmslist = res.data.data.films
    })
  },
  methods: {
    handleClick (data) {
      this.$router.push(`/detail/${data}`)
    }
  }
}
</script>
<style lang="scss">
    .comingSoon{
        width: 100%;
        height: auto;
            ul{
                li{
                    width: 100%;
                    height: 1.2rem;
                    margin-left: 0.1rem;
                    margin-bottom: 0.15rem;
                    .fbox{
                        display: flex;
                        width: 100%;
                        height: 1.2rem;
                        .left{
                            display: block;
                            width: 16.66%;
                            height: 1.2rem;
                        }
                        .center{
                            box-sizing: border-box;
                            padding: 0.05rem 0 0.05rem 0.05rem;
                            width: 66.68%;
                            display: flex;
                            flex-direction: column;
                            justify-content: space-between;
                            .info{
                                flex: 1;
                                width: 100%;
                                height: 25%;
                                line-height: 100%;
                                display: flex;
                                white-space: nowrap;
                                overflow: hidden;
                                span{
                                    align-items: center;
                                    line-height: 0.25rem;
                                }
                                .label{
                                    font-size: 0.13rem;
                                    color: #797d82;
                                }
                                .grade{
                                    color: #ffb232;
                                }
                                .item{
                                    font-size: 0.09rem;
                                    color: #fff;
                                    background-color: #d2d6dc;
                                    height: 0.14rem;
                                    line-height: 0.14rem;
                                    padding: 0 0.02rem;
                                    border-radius: 0.02rem;
                                    margin-top: 0.05rem;
                                }
                                .name{
                                    font-size: 0.16rem;
                                }

                            }
                        }
                    }
                }
        }
        .nomore{
            height: 0.59rem;
            background-color: #ededed;
            color: #bdc0c5;
            font-size: 0.13rem;
            text-align: center;
            margin: auto;
            line-height: 0.59rem;
        }
    }
</style>
